<template>
  <v-app>
    <v-navigation-drawer
      dark
      class="teal darken-1"
      :mini-variant.sync="mini"
      hide-overlay
      mobile-break-point="960"
      app
    >
      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-tile avatar @click.stop="mini = !mini">
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/men/85.jpg">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>Christian Jimenez</v-list-tile-title>
            </v-list-tile-content>

            <v-list-tile-action>
              <v-btn
                icon
                @click.stop="mini = !mini"
              >
                <v-icon>chevron_left</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-toolbar>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          @click.stop="$vuetify.goTo(item.target)"
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-content>
        <router-view></router-view>
    </v-content>

    <v-bottom-nav
      color="teal darken-1"
      class="hidden-md-and-up"
      :active.sync="bottomNav"
      :value="true"
      fixed
    >
      <v-btn
        color="white"
        v-for="item in items"
        :key="item.title"
        @click=""
        flat
        @click="$vuetify.goTo(item.target)"
      >
        <span>{{ item.title }}</span>
        <v-icon>{{ item.icon }}</v-icon>
      </v-btn>
    </v-bottom-nav>
  </v-app>
</template>

<script>
  export default {
    data () {
      return {
        items: [
          { title: 'About Me', icon: 'person', target: '#about' },
          { title: 'My Projects', icon: 'work', target: '#projects' },
          { title: 'Resume', icon: 'school', target: '#resume' },
          { title: 'Contact Me', icon: 'question_answer', target: '#contact' }
        ],
        mini: false,
        right: null,
        bottomNav: 0
      }
    }
  }
</script>

<style>
@media screen and (min-width: 1264px){
  .v-dialog:not(.v-dialog--fullscreen) {
    width: 60% !important;
  }
}
@media screen and (max-width: 1264px){
  .v-dialog:not(.v-dialog--fullscreen) {
    width: 80% !important;
  }
}
@media screen and (max-width: 960px){
  .v-dialog:not(.v-dialog--fullscreen) {
    width: 80% !important;
  }
}
@media screen and (max-width: 600px){
  .v-dialog:not(.v-dialog--fullscreen) {
    width: calc(100% - 10px) !important;
    margin: 0;
    max-height: calc(100% - 10px);
  }
}
</style>



