<template>
  <div>
    <v-card-title
      class="headline teal white--text"
      primary-title
    >
      {{type_viewer}}
    </v-card-title>

    <v-layout wrap>
      <v-flex py-3 pl-3 pr-0 col md8 xs12 id="main-width">
        <div id="main-height" v-if="type_viewer === null"></div>
        <iframe id="main-height" src="/static/3dModels/Vaisseau/vaisseau.html" frameborder="0" width="100%" v-else-if="type_viewer === '3d'">
        </iframe>
        <v-img contain id="main-height" src="/static/img/projects/site4.jpg" v-else-if="type_viewer === 'web'"></v-img>
        <v-img contain id="main-height" src="/static/img/projects/kyt.png" v-else-if="type_viewer === 'game'"></v-img>
      </v-flex>
      <v-flex col md4 xs12>
        <v-card-text>
          <p>
            Lorem ipsum dolor sit amet, csmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
          </p>
          <p>
            <span class="teal--text">Type:</span> Ut enim ad
          </p>
          <p>
            <span class="teal--text">Langage:</span> Lorem ipsucillum
          </p>
          <v-btn block dark color="teal">Download</v-btn>
        </v-card-text>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'Viewer',
  props: ['titre', 'type_viewer'],
  data () {
    return {
      progress: true
    }
  },
  methods: {
    changeHeight () {
      if (window.innerWidth < 960) {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (4 / 3) + 'px'
      } else {
        document.getElementById('main-height').style.height = (window.innerWidth * 0.4) * (9 / 16) + 'px'
      }
    }
  },
  updated () {
    this.changeHeight()
    window.addEventListener('resize', this.changeHeight)
  }
}
</script>

<style scoped>
  @media screen and (max-width: 960px){
    #main-width {
      padding-right: 1.25em !important;
    }
  }
</style>



