<script>
import { validationMixin } from 'vuelidate'
import { required, maxLength, email } from 'vuelidate/lib/validators'
import Viewer from '@/components/Viewer'

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    email: { required, email },
    select: { required },
    checkbox: {
      checked (val) {
        return val
      }
    }
  },
  components: {
    Viewer
  },
  data () {
    return {
      name: '',
      email: '',
      checkbox: false,
      dialog: false,
      titre_viewer: null,
      type_viewer: null,
      projects: [
        { id: 1, title: '3D Space Ship', type: 'games', viewer_type: 'game', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=15', img_lazy: 'https://picsum.photos/10/6?image=15', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 2, title: '3D Space Ship', type: 'web', viewer_type: 'web', icon: 'web', img: 'https://picsum.photos/500/300?image=25', img_lazy: 'https://picsum.photos/10/6?image=25', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 3, title: '3D Space Ship', type: 'design', viewer_type: '3d', icon: 'palette', img: 'https://picsum.photos/500/300?image=35', img_lazy: 'https://picsum.photos/10/6?image=35', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 4, title: '3D Space Ship', type: 'games', viewer_type: 'game', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=45', img_lazy: 'https://picsum.photos/10/6?image=45', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 5, title: '3D Space Ship', type: 'games', viewer_type: 'game', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=55', img_lazy: 'https://picsum.photos/10/6?image=55', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 6, title: '3D Space Ship', type: 'games', viewer_type: 'game', icon: 'videogame_asset', img: 'https://picsum.photos/500/300?image=65', img_lazy: 'https://picsum.photos/10/6?image=65', text: 'Description courte du projet', description: 'Description plus longue du projet' },
        { id: 7, title: '3D Space Ship', type: 'web', viewer_type: 'web', icon: 'web', img: 'https://picsum.photos/500/300?image=75', img_lazy: 'https://picsum.photos/10/6?image=75', text: 'Description courte du projet', description: 'Description plus longue du projet' }
      ],
      projectsShown: [],
      icons: [
        {
          id: 0,
          code: ['fab', 'facebook'],
          color: 'indigo'
        },
        {
          id: 1,
          code: ['fab', 'twitter'],
          color: 'blue'
        },
        {
          id: 2,
          code: ['fab', 'youtube'],
          color: 'red'
        },
        {
          id: 3,
          code: ['fab', 'linkedin'],
          color: 'indigo'
        },
        {
          id: 4,
          code: ['fab', 'github'],
          color: 'brown darken-4'
        }
      ]
    }
  },
  computed: {
    nameErrors () {
      const errors = []
      if (!this.$v.name.$dirty) return errors
      !this.$v.name.maxLength && errors.push('Name must be at most 10 characters long')
      !this.$v.name.required && errors.push('Name is required.')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      return errors
    }
  },
  watch: {
    dialog: function () {
      if (!this.dialog) {
        for (let i = 0; i < this.projectsShown.length; i++) {
          document.getElementsByClassName('hover-menu')[i].classList.remove('opened')
          document.getElementsByClassName('open-viewer')[i].classList.remove('opened')
        }
      }
    }
  },
  methods: {
    submit () {
      this.$v.$touch()
    },
    clear () {
      this.$v.$reset()
      this.name = ''
      this.email = ''
    },
    openDialog (n, title, type) {
      this.titre_viewer = title
      this.type_viewer = type
      this.dialog = true
      document.getElementsByClassName('hover-menu')[n - 1].classList.add('opened')
      document.getElementsByClassName('open-viewer')[n - 1].classList.add('opened')
    },
    filterProjects (filter) {
      for (let item in this.projectsShown) {
        if (this.projectsShown[item].type !== filter) {
          this.projectsShown.splice(item, 1)
          this.filterProjects(filter)
        }
      }

      if (this.projectsShown.length === 0) {
        for (let item in this.projects) {
          if (this.projects[item].type === filter) {
            this.projectsShown.splice(item, 0, this.projects[item])
          }
        }
      }
    },
    resetProjects () {
      this.projectsShown = []
      for (let item in this.projects) {
        this.projectsShown.splice(item, 0, this.projects[item])
      }
    }
  },
  mounted () {
    for (let item in this.projects) {
      this.projectsShown.push(this.projects[item])
    }
  }
}
</script>

